/*base color  http://paletton.com/#uid=1000u0kllllaFw0g0qFqFg0w0aF AA3939*/
* {

	margin: 0;
	padding: 0;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;

}


#root {

	height: 100%;

}


.App {

	display: -ms-flexbox;

	display: flex;
	text-align: center;
	height: 100vh;

}


.sidebar {

	width: 25%;
	background-color: #AA3939;

}


.map {

	width: 75%;
	position: relative;

}


.map-error {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	background-color: #E0E0E0;
	height: 100%;

}


.map-error > p {
	color:#616161;
}


.map-error > h2 {
	color:#616161;
}


.sidebar-header {

	display: -ms-flexbox;

	display: flex;
	min-height: 75px;
	background: #2e3d49;
	position: relative;
	z-index: 11;

}


.title {

	margin-left: 15px;
	margin-right: 15px;
	text-align: center;
	padding-top: 10px;
	color: white;

}


@media screen and (min-width: 1025px) {

	.title {

		padding-top: 0px;
		line-height: 75px;
		margin-left: auto;
		margin-right: auto;
		text-align: center;

	}
}


nav {

	display: -ms-flexbox;

	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-wrap: nowrap;
	    flex-wrap: nowrap;
	color: white;
	z-index: 10;
	position: relative;

}


.search-input {

	border: none;
	margin-top: 20px;
	margin-bottom: 10px;
	border-radius: 4px;
	padding: 0.5em;
	text-align: center;
	color: #801515;
	background-color: #ffaaaa;
	outline-color: #801515;

}

/*https://stackoverflow.com/questions/7381446/center-html-input-text-field-placeholder*/
/*https://davidwalsh.name/html5-placeholder-css*/
.search-input::-webkit-input-placeholder {

	color: #d46a6a;
	text-align: center;

}


.search-input:-moz-placeholder { /* Firefox 18- */

	color: #d46a6a;
	text-align: center;

}


.search-input::-moz-placeholder {  /* Firefox 19+ */

	color: #d46a6a;
	text-align: center;

}


.search-input:-ms-input-placeholder {

	color: #d46a6a;
	text-align: center;

}


.menu-list {

	display: block;
	background-color: #AA3939;
	list-style: none;
	padding:0;
	margin:0;
	-webkit-transition:all 0.5s ease-in-out;
	-o-transition:all 0.5s ease-in-out;
	transition:all 0.5s ease-in-out;
	-webkit-transform: translateY(0px);
	    -ms-transform: translateY(0px);
	        transform: translateY(0px);

}


.menu-items {

	margin-top:10px;
	padding:0 20px;

}


.menu-items:nth-child(1) {

	margin-top: 10px;

}


.menu-items:last-child {

	margin-bottom: 0px;

}


.menu-items a:link {

	color: #ffaaaa;
	text-decoration: none;

}


.menu-items a:visited {

	color: #ffaaaa;

}


.menu-items a:hover {

	color: #550000;

}


#infowindow {

	max-height: 400px ;
	width: 400px;
	overflow-y:auto;

}


@media screen and (max-width: 900px) {

	.App {

		display: -ms-flexbox;

		display: flex;
		-ms-flex-direction: column;
		    flex-direction: column;
		-ms-flex-wrap: nowrap;
		    flex-wrap: nowrap;

	}


	.sidebar {

		height: 75px;
		width: 100%;

	}


	.map {

		height: 100%;
		width: 100%;
		position:relative;
		z-index: 12;

	}


	.sidebar-header {

		display: -ms-flexbox;

		display: flex;
		height: 75px;
		background: #2e3d49;
		position: relative;
		z-index: 11;

	}


	.title {

		margin-left: auto;
		margin-right: auto;
		text-align: center;
		padding-top: 20px;
		color: white;

	}


	@media screen and (max-width:500px) {

		.title {

			text-align: left;
			margin-left: 10px;
			margin-right: 10px;

		}
	}


	#closebtn {

	    height:35px;
	    width:35px;
	    position: absolute;
	    text-decoration:none;
	    top: 17px;
	    right: 10px;
	    font-size: 36px !important;
	    /* margin-right: 10px; */
		/* margin-left: 10px; */
	    z-index:100;
	    cursor:pointer;
	    -webkit-transition: background-position .3s;
	    -o-transition: background-position .3s;
	    transition: background-position .3s;

	}


	.line1,
	.line2,
	.line3 {

		margin-top:7px;
		background-color:#fff;
		width:35px;
		height:3px;
		display:block;
		position:relative;
		opacity:1.0;
		border-radius:15%;
		-webkit-transition: all .3s;
		-o-transition: all .3s;
		transition: all .3s;

	}


	.line1.active {

	  -webkit-transform: rotate(270deg);

	      -ms-transform: rotate(270deg);

	          transform: rotate(270deg);
	  opacity:0.0;
	  top: 10px;

	}


	.line2.active {

	  -webkit-transform: rotate(45deg);

	      -ms-transform: rotate(45deg);

	          transform: rotate(45deg);
	  top: 3px;

	}


	.line3.active {

	  -webkit-transform: rotate(-45deg);

	      -ms-transform: rotate(-45deg);

	          transform: rotate(-45deg);
	  top:-7px;

	}


	nav {

		display: -ms-flexbox;

		display: flex;
		-ms-flex-direction: column;
		    flex-direction: column;
		-ms-flex-wrap: nowrap;
		    flex-wrap: nowrap;
		color: white;
		z-index: 10;
		position: relative;
		-webkit-transition:all 0.5s ease-in-out;
		-o-transition:all 0.5s ease-in-out;
		transition:all 0.5s ease-in-out;
		-webkit-transform: translateY(0px);
		    -ms-transform: translateY(0px);
		        transform: translateY(0px);

	}


	nav.hidden {

		display: -ms-flexbox;

		display: flex;
		-ms-flex-direction: column;
		    flex-direction: column;
		-ms-flex-wrap: nowrap;
		    flex-wrap: nowrap;
		color: white;
		z-index: 10;
		position: relative;
		-webkit-transform: translateY(-1000px);
		    -ms-transform: translateY(-1000px);
		        transform: translateY(-1000px);

	}


	.menu-list {

		display: block;
		background-color: #AA3939;
		list-style: none;
		padding:0;
		margin:0;

	}


	.menu-list.hidden {

		background-color: #AA3939;
		list-style: none;
		padding:0;
		margin:0;

	}


	.search-input {

		border: none;
		margin-top: 20px;
		margin-bottom: 10px;
		border-radius: 4px;
		padding: 0.5em;
		text-align: center;
		color: #801515;
		background-color: #ffaaaa;
		outline-color: #801515;

	}
	/*https://stackoverflow.com/questions/7381446/center-html-input-text-field-placeholder*/
	/*https://davidwalsh.name/html5-placeholder-css*/
	.search-input::-webkit-input-placeholder {

		color: #d46a6a;
		text-align: center;

	}


	.search-input:-moz-placeholder { /* Firefox 18- */

		color: #d46a6a;
		text-align: center;

	}


	.search-input::-moz-placeholder {  /* Firefox 19+ */

		color: #d46a6a;
		text-align: center;

	}


	.search-input:-ms-input-placeholder {

		color: #d46a6a;
		text-align: center;

	}


	.menu-items {

		margin-top:10px;

	}


	.menu-items:nth-child(1) {

		margin-top: 10px;

	}


	.menu-items:last-child {

		margin-bottom: 20px;

	}


	.menu-items a:link {

		color: #ffaaaa;
		text-decoration: none;

	}


	.menu-items a:visited {

		color: #ffaaaa;

	}


	.menu-items a:hover {

		color: #550000;

	}


	#infowindow {

		max-height: 200px ;
		width: 300px;
		overflow-y:auto;

	}

}



